import React from 'react';
import Link from 'next/link';
import logo_main from '../img/logo5.png';
import logo_side from '../img/logo6.png';
import eng from '../img/english.png';
import thai from '../img/thailand.jpg';

if (typeof window !== 'undefined') {
  window.$ = $;
  window.jQuery = $;
  require('materialize-css');
}

class Index extends React.Component {
  componentDidMount() {
    $('.button-collapse').sideNav();
  }
  render() {
    return (
      <nav class="main-header">
        <div class="container">
          <div class="nav-wrapper">
            <img src={logo_main} className="brand-logo logo-img" />
            <a
              href="#"
              data-activates="side-nav"
              class="button-collapse show-on-large right"
            >
              <i class="material-icons">menu</i>
            </a>
            <ul class="right hide-on-med-and-down">
              <li class="active">
                <a href="index.html">Dashboard</a>
              </li>
              <li>
                <a href="posts.html">Posts</a>
              </li>
              <li>
                <a href="categories.html">Categories</a>
              </li>
              <li>
                <a href="comments.html">Comments</a>
              </li>
              <li>
                <a href="users.html">Users</a>
              </li>
            </ul>
            <ul id="side-nav" class="side-nav">
              <h4 class="center">
                <img src={logo_side} class="logo-img" />
              </h4>
              <li>
                <div class="divider" />
              </li>
              <li>
                <a href="index.html">
                  <i class="fa fa-home grey-text text-darken-4" /> HOME
                </a>
              </li>
              <li>
                <a href="solutions.html">
                  <i class="fa fa-cog grey-text text-darken-4" /> PORTFOLIO
                </a>
              </li>
              <li>
                <a href="about.html">
                  <i class="fa fa-users grey-text text-darken-4" /> ABOUT
                </a>
              </li>
              <li>
                <a href="resume.html">
                  <i class="fa fa-users grey-text text-darken-4" /> RESUME
                </a>
              </li>
              <li>
                <div class="divider" />
              </li>
              <li>
                <a href="#contact-modal" class="btn green modal-trigger">
                  CONTACT
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    );
  }
}

export default Index;
